const styles = theme => ({
  button: {
    margin: theme.spacing.unit
  },
  tableButtons : {
    margin: 3,
  },
  chip: {
    margin: theme.spacing.unit,
  },
  DsName: {
    fontSize: theme.typography.fontSize,
    fontWeight: theme.typography.fontWeightNormal,
  }
})

class ListDataSetTable extends React.Component {
  static propTypes = {
    DatasetList: PropTypes.array,
    handleClickHistory: PropTypes.func,
    newStatus: PropTypes.string,
  }

  state = {
    menuAnchor: null,
    currentDataset: "",
    currentBranch: ""
  }

  onShowChipMenu = (datasetName, branchName, e) => {
    this.setState({
      menuAnchor: e.target,
      currentDataset: datasetName,
      currentBranch: branchName
    })
  }

  onCloseChipMenu = () => {
    this.setState({
      menuAnchor: false,
      currentDataset: "",
      currentBranch: ""
    })
  }
  
  onShowEditMenu = (datasetName, e) => {
    this.setState({
      menuAnchor: e.target,
      currentDataset: datasetName
    })
  }

  onCloseEditMenu = () => {
    this.setState({
      menuAnchor: false,
      currentDataset: ""
    })
  }

  render() {
    const {
      classes,
      DatasetList,
      handleClickHistory,
      newStatus,
      // Cache_Version_History
    } = this.props

    // const {
    //   currentDataset,
    //   currentBranch
    // } = this.state

    return (
      <Fragment>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Job ID</TableCell>
              <TableCell>Job Name</TableCell>
              <TableCell style={{ width: "39%" }}>Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {DatasetList.map((item, index) => (
              <TableRow key={index}>
                <TableCell component="th" scope="row" className={classes.DsName}>
                  {item["job_id"]}
                </TableCell>
                <TableCell>
                  {item["job_name"]}
                </TableCell>
                <TableCell>
                  {newStatus
                    ? newStatus
                    : item["status"]
                  }
                  {item["job_id"]
                    ? (
                      <Button
                        className={classes.tableButtons}
                        variant="contained"
                        color="secondary"
                        onClick={() => handleClickHistory(item)}
                      >
                        Refresh Status
                      </Button>
                      )
                    : ""
                  }
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Fragment>
    )
  }
}

export default withStyles(styles)(ListDataSetTable);
